<template >
<div class="header">
  <div class="content-wrapped">
    <div class="avatar">
      <img width="64px" height="64px" :src="seller.avatar" alt="">
    </div>
    <!-- avatar结束 -->
    <div class="content">
      <div class="title">
        <span class="brand"></span>
        <span class="name">{{seller.name}}</span>
      </div>
      <div class="description">
        {{seller.description}}/{{seller.deliveryTime}}分钟送达
      </div>
      <div class="support" v-if="seller.supports">
        <span class="icon" :class="classMap[seller.supports[0].type]"></span>
        <span class="text">{{seller.supports[0].description}}</span>
      </div>
    </div>
    <!-- content结束 -->
    <div v-if="seller.supports" class="support-count" @click="showDetail">
      <span class="count">{{seller.supports.length}}个</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <!-- support-count结束 -->
  </div>
  <!-- content-wrapped结束 -->
  <div class="bulletin-wrapped" @click="showDetail">
    <span class="bulletin-title"></span> <span class="bulletin-text">{{seller.bulletin}}</span>
    <i class="icon-keyboard_arrow_right"></i>
  </div>
  <!-- bulletin结束 -->
  <div class="background">
    <img :src="seller.avatar" width="100%" height="100%" alt="">
  </div>
  <!-- background结束 -->
  <div class="detail" v-show="deltailShow" transition="fade">
    <div class="detail-wrapper clearfix">
      <div class="detail-main">
        <h1 class="name">{{seller.name}}</h1>
        <div class="star-wrapper">
          <star :size="48" :score="seller.score"></star>
        </div>
        <div class="title">
          <div class="line">
          </div>
          <div class="text">
            优惠信息
          </div>
          <div class="line">
          </div>

        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item" v-for="item in seller.supports">
            <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
            <span class="text">{{seller.supports[$index].description}}</span>
          </li>
        </ul>
        <div class="title">
          <div class="line">
          </div>
          <div class="text">
            商家公告
          </div>
          <div class="line">
          </div>

        </div>
        <div class="bulletin">
          <p class="content">{{seller.bulletin}}</p>

        </div>
      </div>
    </div>
    <!-- detail-wrapper结束 -->
    <div class="detail-close" @click="hiddenDetail">
      <i class="icon-close"></i>
    </div>
  </div>
  <!-- details结束 -->
</div>
<!-- header结束 -->
</template>
<script type="text/ecmascript-6">
import star from "../star/star.vue"
export default {
  props: {
    seller: {
      type: Object
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  },
  methods: {
    showDetail: function() {
      this.deltailShow = true
    },
    hiddenDetail: function() {
      this.deltailShow = false
    },
  },
  components: {
    star
  },
  data() {
    return {
      deltailShow: false
    }
  }
};
</script>

 <style lang="stylus" rel="stylesheet/stylus" >
 @import "../../common/stylus/mixin.styl"
 .header
  color:#fff;
  position:relative
  background-color:rgba(7,17,27,0.5)
  /*background-image:url("http://static.galileo.xiaojukeji.com/static/tms/seller_avatar_256px.jpg")*/
  overflow:hidden
  .content-wrapped
   position: relative
   padding:24px 12px 18px 24px
   font-size:0
   .avatar
    display:inline-block
    img
     border-radius:2px
   .content
    display:inline-block
    vertical-align:top
    margin-left:16px
    .title
     margin:2px 0px 8px 0px
     .brand
      display:inline-block
      width:30px
      height:18px
      bg_img(brand)
      background-size:30px 18px
      background-repeat:no-repeat
      vertical-align:top
     .name
      font-size:16px
      color:rgb(255,255,255)
      font-weight:bold
      line-height:18px
      margin-left:6px
    .description
     font-size:12px
     color:rgb(255,255,255)
     line-height:12px
     margin-bottom:10px
    .support
     .icon
       width:12px
       height:12px
       display:inline-block
       margin-bottom:2px
       margin-right:4px
       background-size:12px 12px
       background-repeat:no-repeat
       &.decrease
        bg_img("decrease_1")
       &.discount
         bg_img("discount_1")
       &.guarantee
         bg_img("guarantee_1")
       &.invoice
         bg_img("invoice_1")
       &.special
         bg_img("special_1")
     .text
      font-size:10px
      color:rgb(255,255,255)
      line-height:12px
      vertical-align:top
   .support-count
    position: absolute
    right: 12px
    bottom: 14px
    padding: 0 8px
    height: 24px
    line-height: 24px
    border-radius: 14px
    background: rgba(0, 0, 0, 0.2)
    text-align: center
    .count
      vertical-align: top
      font-size: 10px
    .icon-keyboard_arrow_right
      margin-left: 2px
      line-height: 24px
      font-size: 10px
  .bulletin-wrapped
   position:relative
   height:28px
   background-color:rgba(7,17,27,0.2)
   line-height:28px
   padding:0px 22px 0px 12px
   white-space:nowrap
   overflow:hidden
   text-overflow:ellipsis
   .bulletin-title
     display:inline-block
     width:22px
     height:12px
     bg_img("bulletin")
     background-size:22px 12px
     background-repeat:no-repeat
     vertical-align:top
     margin-top:8px
   .bulletin-text
    font-size:10px
    font-weight:200px
    line-height:28px
    margin:0px 4px
    vertical-align:top
   .icon-keyboard_arrow_right
    position:absolute
    right:12px
    bottom:7px
  .background
   position:absolute
   top:0px
   left:0px
   width:100%
   height:100%
   z-index:-1
   filter:blur(10px)
  .detail
   position:fixed
   z-index:20
   width:100%
   height:100%
   overflow:auto
   backdrop-filter:blur(10px)
   transition:all 0.6s
   &.fade-transition
    opacity:1
    background-color:rgba(7,17,27,0.95)
   &.fade-enter,&.fade-leave
    opacity:0
    background-color:rgba(7,17,27,0)
   top:0px
   left:0px
   .detail-wrapper
    min-height:100%
    width:100%
    .detail-main
      margin-top:64px
      padding-bottom:64px
      .name
       font-size:16px
       line-height:16px
       text-align:center
       margin-bottom:16px
      .star-wrapper
        padding:2px 0px
        text-align:center
      .title
       display:flex
       width:80%
       margin:30px auto 24px auto
       .line
