<template >
  <div class="goods">
    <div class="menu-wrapper" v-el:menu-wrapper>
     <ul>
       <li v-for="item in goods" class="menu-item " :class="{'current':currentIndex===$index}" @click="selectMenu($index,$event)">
        <span class="text border-1px"><span v-if="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name }}</span>
       </li>
     </ul>
     </div>
    <div class="foods-wrapper" v-el:foods-wrapper  >
      <ul>

        <li v-for="item in goods" class="food-list  food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li  @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
              <div class="icon">
                <img :src="food.icon" width="57px" height="57px">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="description">{{food.description}}</p>

              <div class="extra">
                <span style="margin-right:12px">月售{{food.sellCount}}份</span
                ><span >好评率{{food.rating}}%</span>
              </div>
              <div class="price">
                <span class="new">￥{{food.price}}</span>
                <span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
                </div>
              <div class="cartcontrol-wrapper">
               <cartcontrol :food="food"></cartcontrol>
              </div>

            </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart v-ref:shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice"  :min-price="seller.minPrice"></shopcart>
    <food :food="selectedFood"  v-ref:food ></food>
  </div>

</template>

<script type="text/ecmascript-6">
import BScroll from "better-scroll";
import shopcart from 'components/shopcart/shopcart';
import food from 'components/food/food';
import  cartcontrol from 'components/cartcontrol/cartcontrol';
const ERR_OK=0;
export default {
  components:{ shopcart ,cartcontrol,food},
  props:{
    seller:{
      type:Object
    }
  },
  events:{
    'cart.add'(target){
      this._drop(target)
    }
  },
  data(){
    return {
      goods:[],
      listHeight:[],
      scrollY:0,
      selectedFood:{}

    }
  },
  created(){
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special'];
    this.$http.get("/api/goods").then((response)=>{
    response=response.body;

    if(response.errno===ERR_OK){
      this.goods=response.data;
      this.$nextTick(()=>{
          this._initScroll();
          this._calculateHeight();
      })
    }
    });
  },
  computed:{
    selectFoods(){
      let foods=[];
      this.goods.forEach((good)=>{
        good.foods.forEach((food)=>{
          if(food.count){
            foods.push(food);
          }
        })
      })
      return foods;
    },
    currentIndex(){
      for(let i=0;i<this.listHeight.length;i++){
       let height1=this.listHeight[i];
       let height2=this.listHeight[i+1];
       if(!height2||(this.scrollY>=height1&&this.scrollY<height2)){
         return i;
       }
      }

    }
  },
  methods:{
    selectFood(food,event){
      if(!event._constructed){
        return;
      };
       this.selectedFood=food;
       this.$refs.food.show();

    },

    _drop(target){
      this.$nextTick(()=>{
          this.$refs.shopcart.drop(target);
      })

    },
     selectMenu(index,event){
       if(!event._constructed){
         return;
       };
         let foodList=this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
         let el=foodList[index];
         this.foodsScroll.scrollToElement(el, 300);

     },
    _initScroll(){
      this.menuScrool=new BScroll(this.$els.menuWrapper,{click:true})
      this.foodsScroll=new BScroll(this.$els.foodsWrapper,{probeType:3,click:true})
      this.foodsScroll.on('scroll',(pos)=>{
        this.scrollY=Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight(){
      let foodList=this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
      let height=0;
        this.listHeight.push(height);
      for(let i=0;i<foodList.length;i++){
        let item = foodList[i];
        height+=item.clientHeight;
        this.listHeight.push(height);
      }
    }
  }
}

</script>
 <style lang="stylus" rel="stylesheet/stylus" >
 @import "../../common/stylus/mixin.styl"
 .goods
  position:absolute
  top:174px
  bottom:46px
  display:flex
  overflow:hidden
  .menu-wrapper
    flex:0 0 80px
    width:80px
    background-color:#f3f5f7
    .menu-item
      display:table
      height:54px
      width:56px
      line-height:14px
      padding:0 12px
      &.current
        position:relative
        margin-top:-1px
        background-color:#fff
        font-weight:700
        z-index:10
        .text
         border-none()
      .icon
        display:inline-block
        vertical-align:top
        width:12px
        height:12px
        display:inline-block
        margin-bottom:2px
        margin-right:2px
        background-size:12px 12px
        background-repeat:no-repeat
        &.decrease
         bg_img("decrease_3")
        &.discount
          bg_img("discount_3")
        &.guarantee
          bg_img("guarantee_3")
        &.invoice
          bg_img("invoice_3")
        &.special
          bg_img("special_3")
      .text
        display:table-cell
        font-size:12px
        width:56px
        vertical-align:middle
        border-1px(rgba(7,17,27,0.1))
  .foods-wrapper
    flex:
    .title
      padding:14px 14px
      color:rgb(147,153,159)
      height:26px
      font-size:12px
      line-height:26px
      background-color:#f3f5f7
      border-left:2px solid #d9dde1
    .food-item
      padding:18px
      display:flex
      border-1px(rgba(7,17,27,0.1))
      &.last-child
        bordernone()
        padding-bottom:0px
      .icon
        flex:0 0 57px
        margin-right:10px
      .content
        flex:1
        .name
          font-size:14px
          color:rgb(7,17,27)
          line-height:14px
          margin:2px 0 8px 0
        .description
         font-size:10px
         color:rgb(147,153,159)
         line-height:10px
         margin:8px 0
         line-height:12px
        .extra
          font-size:10px
          color:rgb(147,153,159)
          line-height:10px
          margin:8px 0
        .price
         font-weight:700
         line-height:24px
         .new
          font-size:14px
          color:red
          margin-right:8px
         .old
          text-decoration:line-through
          font-size:10px
          color:rgb(147,153,159)
        .cartcontrol-wrapper
         position:absolute
         bottom:16px
         right:16px







</style>



